module.exports = "\n      <div class=\"parameterl\">\n               \n                <div class=\"explaininfo\">\n                    <p>1.替换元素是浏览器根据其标签的元素与属性来判断显示具体的内容。典型的可替换元素有 \"img\"、 \"object\"、 \"video\" 和 表单元素，如\"textarea\"、 \"input\" 。 某些元素只在一些特殊情况下表现为可替换元素，例如 \"audio\"和 \"canvas\"</p>\n                </div>\n                <div class=\"explaininfo\">\n                    <p>1.1-----根据input标签的type属性来决定</p>\n                    <input type=\"text\" value=\"111\" />\n                    <input type=\"password\" value=\"111\"/>\n                    <input type=\"radio\" value=\"111\"/>\n                    <p>1.2-----src属性的值来读取图片信息</p>\n                    <img src=\"xxxHTMLLINKxxx0.449582073604688050.7802753550931811xxx\" class=\"imgstyle\" />\n                </div>\n<pre>\n<code class=\"html\">\n   <div>\n        <input type=\"text\" value=\"111\" />\n        <input type=\"password\" value=\"111\"/>\n        <input type=\"radio\" value=\"111\"/>\n        <img src=\"xxxHTMLLINKxxx0.216818797402083870.1401365192141384xxx\"  class=\"imgstyle\"/>\n    </div>\n</code>\n<code class=\"css\">\n    .imgstyle{\n     border:1px solid #ccc;\n     display:inline-block;\n     width:50px;\n     height:50px;\n    }\n</code>\n\n</pre>\n\n\n                <div class=\"explaininfo\">\n                    <p>2.非替换元素是将内容直接告诉浏览器，将其显示出来。</p>\n                </div>\n                <div class=\"divwh\">\n                    <p>我是p段落</p>\n                    <label>我是label</label>\n                </div>\n<pre>\n<code class=\"html\">\n   <div>\n        <p>我是p段落</p>\n        <label>我是label</label>\n    </div>\n</code>\n\n</pre>\n</div>\n\n\n";


